export default class FBConfig {
    //Info user
    static fbName: any;
    static fbPhoto: any;
    static fbScore = 0;
    static metadata = 0;
    static numberTriedAds = 0;

    //Ads
    static preloadedInterstitial: FBInstant.AdInstance = null!;
    static preloadedRewardedVideo: FBInstant.AdInstance = null!;
    static videoAdsID = "";
    static interstitialId = "";
    static bannerId = "";

    static getBase64Image() {
        return 'data:image/webp;base64,';
    }

    static isSupportRewardVideo(): boolean {
        return FBInstant.getSupportedAPIs().indexOf('getRewardedVideoAsync') !== -1
    }

    static isSupportBannerAds(): boolean {
        return FBInstant.getSupportedAPIs().indexOf('loadBannerAdAsync') !== -1
    }

    static isSupportInterstitial() {
        return FBInstant.getSupportedAPIs().indexOf('getInterstitialAdAsync') !== -1
    }
}